.search_container{
    max-width: 24rem;
    overflow: hidden;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    margin-left: auto;
    margin-right: auto;
    
}
.form{
    width: 100%;
    max-width: 24rem;
}
.text_placeholder{
    appearance: none;
    background: transparent;
    border: none;
    width: 100%;
    color: grey;
    font-weight: 700;
 margin-right: 0.75rem;
 padding-top: 0.25rem;
 padding-bottom: 0.25rem;
 padding-left: 0.5rem;
 padding-right: 0.5rem;
 line-height: 1.25;
 border-bottom: 2px solid #38b2ac;

}

.text_placeholder:focus{
    outline: none;
}



.form_container{display: flex;
    align-items: center;
    border-bottom-width: 2px;
    border-color: #38b2ac;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}
.form_btn{
    flex-shrink: 0;
    background-color: #38b2ac;
    border:#38b2ac;
    padding: 8px;
    color: #fff;
}
.form_btn:hover{
    background-color: #2c7a7b;
}